Vue.filter('tabFromatter', function(post){
  if(post.good == true){
    return ''
  }else if(post.top == true){
    return ''
  }else if(post.tab == 'ask'){
    return ''
  }else if(post.tab == 'share'){
    return ''
  }else{
    return ''
  }
})


